
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif;
}

body {
  background: #ffffff;
  color: #000000;
}

nav {
  background: #ffffff;
  padding: 1rem;
  border-bottom: 1px solid #e0e0e0;
  position: sticky;
  top: 0;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 1rem;
}

nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  color: #000000;
  font-size: 1.1rem;
  font-weight: 500;
}

button {
  padding: 0.5rem 1rem;
  margin-left: 0.5rem;
  border: 1px solid #e0e0e0;
  border-radius: 3px;
  background: #ffffff;
  color: #000000;
  cursor: pointer;
  font-size: 0.9rem;
  transition: background 0.2s ease;
}

button:hover {
  background: #f5f5f5;
}

main {
  padding: 4rem 0;
  text-align: center;
}

main h1 {
  margin-bottom: 1rem;
  font-size: 2.5rem;
  font-weight: 600;
}

main p {
  margin-bottom: 2rem;
  color: #666666;
  font-size: 1.1rem;
}

main button {
  background: #000000;
  color: #ffffff;
  border: none;
  padding: 0.75rem 1.5rem;
}

main button:hover {
  background: #333333;
}

.auth-container {
  max-width: 400px;
  margin: 0 auto;
  padding: 2rem;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 2rem 0;
}

.auth-form input {
  padding: 0.75rem;
  border: 1px solid #e0e0e0;
  border-radius: 3px;
  font-size: 1rem;
}

.switch-auth {
  background: none;
  border: none;
  color: #666;
  text-decoration: underline;
  cursor: pointer;
}

.user-email {
  margin-right: 1rem;
  color: #666;
}

.error-message {
  color: #ff0000;
  margin-bottom: 1rem;
  font-size: 0.9rem;
}

.main-content {
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
}

.main-content form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 20px;
}

.main-content input,
.main-content textarea {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.main-content button {
  padding: 10px;
  background: #0066cc;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
